<template>
  <div id="app">
    <notification></notification>

    <header class="mb-6 mt-6">
      <h1 class="title"><span v-if="$i18n.locale === 'zh-cn'">雾盘 </span>Klouderr</h1>
      <languageSwitcher></languageSwitcher>
    </header>

    <Adsense data-ad-client="ca-pub-4679085340013866" data-ad-slot="9178412838"></Adsense>

    <article class="message max-w-md mr-auto ml-auto">
      <div class="message-body">
        <p>
          Klouderr is the fastest and most secure cloud hosting to share your files.
          Whether you want to send a confidential contract, over 100 holiday photos or
          a multi-gigabyte video, then Klouderr is the cloud service that fulfills your needs.
        </p>

        <p>
          There are some other cloud services that you can use to transfer files
          (WeTransfer, HighTail, ...),
          but we think our cloud service is easier to use, faster and more secure. But don't
          take our word for it. Try it yourself and send your next transfer using Klouderr.
        </p>
      </div>
    </article>

    <uploader @uploaded="onFileUploaded"></uploader>
    <uploaded-file-list :files="uploadedFiles"></uploaded-file-list>
    <router-view/>
  </div>
</template>

<script>
import Uploader from '@/components/Uploader.vue';
import UploadedFileList from '@/components/UploadedFiles.vue';
import Notification from '@/components/Notification.vue';
import LanguageSwitcher from '@/components/LanguageSwitcher.vue';

export default {
  data: () => ({
    uploadedFiles: [],
    locale: '',
  }),
  mounted() {
    this.locale = this.$i18n.locale;
  },
  metaInfo() {
    return {
      // if no subcomponents specify a metaInfo.title, this title will be used
      title: '',
      // all titles will be injected into this template
      titleTemplate: titleChunk => (this.locale === 'zh-cn'
        ? `${titleChunk} 雾盘 Klouderr`
        : `${titleChunk} Klouderr`),
    };
  },
  components: {
    uploader: Uploader,
    'uploaded-file-list': UploadedFileList,
    notification: Notification,
    languageSwitcher: LanguageSwitcher,
  },
  methods: {
    onFileUploaded(file) {
      this.uploadedFiles.unshift(file);
    },
  },
};
</script>


