<template>
    <div class="flex justify-center align-center">
        <span class="flex align-center mt-1 mr-8">{{ $t('language') }}</span>
        <button class="button is-rounded mr-6"
            :class="{ 'is-white': $i18n.locale !== 'zh-cn', 'is-dark': $i18n.locale === 'zh-cn' }"
            @click="switchLang('zh-cn')">中文</button>
        <button class="button is-rounded"
            :class="{ 'is-white': $i18n.locale !== 'en', 'is-dark': $i18n.locale === 'en' }"
            @click="switchLang('en')">English</button>
    </div>
</template>

<script>
export default {
  mounted() {
    const userLocale = localStorage.getItem('locale');
    if (userLocale) {
      this.switchLang(userLocale);
    }
  },
  methods: {
    switchLang(lang) {
      this.$i18n.locale = lang;
      localStorage.setItem('locale', lang);
    },
  },
};
</script>
